.el-switch {
  display: inline-flex;
  align-items: center;
  position: relative;
  font-size: 14px;
  line-height: 20px;
  height: 20px;
  vertical-align: middle; }
  .el-switch.is-disabled .el-switch__core,
  .el-switch.is-disabled .el-switch__label {
    cursor: not-allowed; }
  .el-switch__label {
    transition: .2s;
    height: 20px;
    display: inline-block;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    vertical-align: middle;
    color: #303133; }
    .el-switch__label.is-active {
      color: #199c99; }
    .el-switch__label--left {
      margin-right: 10px; }
    .el-switch__label--right {
      margin-left: 10px; }
    .el-switch__label * {
      line-height: 1;
